import React from 'react';
import { Tab, Tabs, Typography } from "@material-ui/core";
import TideTimesGrid from '../TideTimesGrid/TideTimesGrid';

class TideTimesLayout extends React.Component {

    state = {
        siteToDisplay: '',
        siteDate: {}
    }

    componentDidMount() {
        this.setState({siteData: this.props.siteData, siteToDisplay: Object.keys(this.props.siteData)[0]});
    }

    handleTabChange = (event, site) => {
        this.setState({ siteToDisplay: site });
      };

    render() {
        
        const {siteData, siteToDisplay} = this.state;

        if (!siteData) {
            return <Typography variant="h3">No Data</Typography>;
        }

        return (
            <div>
            <Tabs value={siteToDisplay}  onChange={this.handleTabChange} >
                {
                    Object.keys(siteData).map((key, index) => {
                        return (<Tab label={key} key={index}/>);
                    })
                }
            </Tabs>
            {
                (siteToDisplay === '')
                    ? <div>Nothing to see</div>
                    : <TideTimesGrid data={siteData[siteToDisplay]}/>
            }
            </div>
        );
    }

}
 
export default TideTimesLayout;